// wpo-header-style-2

.wpo-header-style-2 {
    position: absolute;
    left: 0;
    top: 30px;
    width: 100%;
    z-index: 99;
    background: transparent;

    .navigation {
        background: $white;
        border-radius: 5px;

        &.sticky-on {
            background: $white;
        }

        .navbar-header {
            .navbar-brand {
                margin-left: 5px;

                @media (max-width:991px) {
                    margin-left: 0;
                }
            }
        }

    }

    @media screen and (min-width: 992px) {
        .wpo-site-header #navbar>ul .sub-menu {
            background: $white;

            a {
                color: $dark-gray;
            }
        }
    }

    #navbar>ul>li>a {
        color: $dark-gray;
    }

    .header-right .cart-toggle-btn,
    .header-right .search-toggle-btn {
        background-color: rgba(255, 255, 255, 0.1);
        color: $white;

        .fi {
            color: $white;
        }
    }
}



// wpo-site-header-s1
.wpo-site-header-s1 {
    position: absolute;
    width: 100%;
    z-index: 99;

    @media (min-width: 1200px) {
        padding: 0 100px;
    }

    @media (max-width:1199px) {
        padding: 0 30px;
    }

    @media (max-width:991px) {
        padding: 0;
        position: relative;
        background: $dark-gray;
    }


    #navbar>ul>li>a {
        @media screen and (min-width: 992px) {
            color: $white;

            &:hover,
            &.active {
                color: $theme-primary-color-s2;
            }
        }
    }

    .theme-btn {
        @media (min-width: 1400px) {
            padding: 3px 13px;
        }

        @media (max-width: 1600px) {
            padding: 3px 13px;
            font-size: 15px;
        }
    }

    .header-right>div:last-child {
        @media (max-width:1399px) {
            margin: 0;
        }
    }

    .header-right .close-form .theme-btn {

        @media (max-width: 1399px) {
            font-size: 13px;
            padding: 3px 13px;

        }

        @media (max-width:1199px) {
            padding: 3px 13px;
            font-size: 12px;
        }

        @media (max-width: 450px) {
            padding: 5px 10px;
        }
    }

    @media (max-width: 450px) {
        .header-right {
            right: -20px;
        }
    }

    .navigation.sticky-header {
        background: linear-gradient(to bottom, #773093, #2C1237);
    }
}


// wpo-site-header-s2

.wpo-site-header-s2 {
    #navbar>ul>li {
        a {
            padding: 22px 22px;

            @media (max-width: 1400px) {
                padding: 30px 10px;
                font-size: 16px;
            }

            @media (max-width: 991px) {
                padding: 15px 30px;
            }
        }

        &:first-child {
            >a {
                @media (min-width: 991px) {
                    padding-left: 0;
                }
            }
        }
    }

    #navbar {
        justify-content: flex-start;
    }

    .container {
        width: 100%;
        justify-content: space-between;
        display: block;
    }

    .row {
        width: unset;
    }

    .header-right {
        >div {
            margin: 0 30px;
            position: relative;

            @media (max-width: 575px) {
                margin-right: 10px;
            }

            &+div {
                &::before {
                    position: absolute;
                    left: -20px;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 1px;
                    height: 20px;
                    content: "";
                    background: $border-color-s2;
                }

            }

        }

        .search-toggle-btn .fi {
            margin-right: 0;
        }

        .search-toggle-btn .fi {
            color: $dark-gray;
        }

        .language {
            @media (max-width: 991px) {
                display: none;
            }

            select {
                border: 0;
                text-transform: uppercase;
                width: 100%;
                height: 68px;

                &:focus {
                    outline: none;
                }
            }
        }

        .social {
            @media (max-width: 575px) {
                display: none;
            }

            ul {
                list-style: none;
                display: flex;
                align-items: center;

                li {
                    a {
                        color: #888888;

                        &:hover {
                            color: $theme-primary-color;
                        }
                    }

                    &+li {
                        margin-left: 15px;
                    }
                }
            }
        }
    }
}


// wpo-site-header-s3

.wpo-site-header-s3 {
    position: absolute;
